<template>
<div>
    <div class="food-stock clearfix">
        <div class="stock-title food-left left select">
            <span>日备货量数</span>
        </div>
        <ul class="stock-content food-right left clearfix">
            <li v-for="(item,index) in stockOptions" 
                :key="item.value"
                class="left"
                :class="{active:item.value === stockSelect}"
                @click="changeSelect(index)">
                {{ item.label }}
            </li>
        </ul>
    </div>
    <div class="food-stock clearfix" v-show="isShowinput">
        <div class="stock-title food-left left input">
            <span></span>
        </div>
        <div class="stock-content food-right left">
            <el-input 
            @keyup.native = "checkStockNum"
            v-model="stockNum" 
            placeholder="请输入数量"
            @change="changeStockT"></el-input>
        </div>
    </div>
</div>

</template>

<script>
import { GoodList } from "../index";

export default {
  props: ["stockNumber"],

  data() {
    return {
      stockNum: 0,
      stockOptions: [
        {
          label: "不限量",
          value: 0
        },
        {
          label: "限量",
          value: 1
        }
      ],
      stockSelect: 0,
      isShowinput: false
    };
  },
  watch: {
    stockSelect(val) {
      if (val === 0) {
        this.isShowinput = false;
        this.$emit("change-stocknum", 0);
      } else if (val === 1) {
        this.isShowinput = true;
        this.$emit("change-stocknum", this.stockNum);
      }
    },
    stockNumber(val) {
      if (val !== 0) {
        this.isShowinput = true;
        this.stockSelect = 1;
      }
      this.stockNum = val;
    }
  },
  methods: {
    checkStockNum() {
      this.stockNum = GoodList.checkRound(this.stockNum);
      this.changeStock();
    },
    changeSelect(index) {
      this.stockSelect = this.stockOptions[index].value;
    },
    changeStock() {
      this.$emit("change-stocknum", this.stockNum);
    },
    changeStockT(val) {
      if (val === "") {
        this.$emit("change-stocknum", "");
      }
    }
  }
};
</script>

<style lang="scss" scoped>
@import "src/styles/mixin.scss";

.food-left {
  width: 220px;
  text-align: right;
  opacity: 0.5708;
  margin-right: 14px;
}
.food-right {
  @include fc(14px, #666);
  min-width: 1020px;
  max-width: 1021px;
}
.stock-title.input {
  line-height: 34px;
  height: 34px;
}
.stock-title.select {
  line-height: 30px;
  height: 30px;
}
ul.stock-content {
  margin-bottom: 10px;
  li {
    @include wh(60px, 30px);
    border: 1px solid #d8d8d8;
    border-radius: 2px;
    text-align: center;
    line-height: 30px;
    cursor: pointer;
    margin-right: 10px;
  }
  li.active {
    background: $blueA;
    color: #fff;
    border: 1px solid $blueA;
  }
}
</style>



